* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  position: relative;
}

p {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 500;
}


h2 {
  color: #fff;
  margin-bottom: 2rem;
  margin-top: 4rem;
  font-size: 2rem;

}

.app {
  background-image: url('./assets/sunny-bg.jpg');
  background-size: cover;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  transition: 0.4s ease-out;
}

.app.warm {
  background-image: url('./assets/cloudy-bg.jpg');
  transition: 0.4s ease-out;
}


.info {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  top: 50rem;
}

.info__degree {
  margin-left: 12rem;
  font-size: 16rem;
}

.info__degree::after{
  content: "\00b0";
  margin-right: 5rem;
}

.info__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40rem;
}

.info__city {
  font-size: 6rem;
  margin-right: 5rem;
  padding-right: 10rem;
}

.info__time {
  font-size: 2rem;
  margin-right: 8rem;
}

.info__condition {
  font-size: 2rem;
}

.info__logo{
  transition: 0.4s ease-out;
  width: 64px;
  height: 64px;
  margin-top: 2rem;
}

.info__row {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.search-box {
  width: 100%
  
}

.mg {
  padding-top: 2rem;
}

.right-box {
  position: absolute;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  height: 100vh;
  margin: 0;
  padding: 50px;
  width: 30%;
}

.search-box {
  width: 100%;
  margin: 20px auto
}

::placeholder {
  color: lightgray;
  opacity: 0.8;
  font-size: 1.6rem;
}

.search-box .search-bar {
  display: block;
  width: 100%;

  appearance: none;
  outline: none;
  border: none;
  background: none;

  color: #fff;
  font-size: 2rem;
  transition: 0.4s ease;
}

.search-box::after {
  content: "";
  border-bottom: solid 1px #fff;
  left: 20px;
  position: absolute;
  width: 87%;
  margin: 8px;
}

.list-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}


.text {
  color: lightgray;
}

@media screen and (max-width: 768px) {
  p {
    color: #fff;
    font-size: 1.4rem;
    font-weight: 300;
  }

  h2 {
    color: #fff;
    font-size: 1.8rem;
  }
  
  .app {
    height: 100vh;
  }
  .info {
    display: flex;
    flex-flow: nowrap column;
    justify-content: center;
    align-items: center;
    top: 10px;
    left: 5rem;
    margin: 5rem;
    margin-bottom: 1rem;
  }

  .info__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .right-box {
    position: absolute;
    bottom: 80px;
    height: 30vh;
    background: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding-top: 10px;
    width: 100%;
  }

  .info__degree {
    font-size: 12rem;
    text-align: center;
    margin-left: 2rem;
  }

  .info__degree::after{
    margin-right: 0;
  }

  .info__city {
    font-size: 4rem;
    margin: 2rem 0;
    padding: 0;
    text-align: center;
  }

  .info__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  
.info__time {
  font-size: 1.6rem;
  margin-right: 0;
  text-align: center;
}

}

@media screen and (max-width: 500px) {
  p {
    color: #fff;
    font-size: 1.4rem;
    font-weight: 300;
  }

  h2 {
    color: #fff;
    font-size: 1.8rem;
  }
  
  .app {
    height: 100vh;
  }
  .info {
    display: flex;
    flex-flow: wrap column;
    justify-content: center;
    align-items: center;
    top: 0;
    margin: 5rem auto;
    margin-bottom: 1rem;
  }

  .info__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .right-box {
    position: absolute;
    bottom: 80px;
    height: 30vh;
    background: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding-top: 10px;
    width: 100%;
  }

  .info__degree {
    font-size: 12rem;
    text-align: center;
    margin-left: 2rem;
  }

  .info__degree::after{
    margin-right: 0;
  }

  .info__city {
    font-size: 4rem;
    margin: 2rem 0;
    padding: 0;
    text-align: center;
  }

  .info__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  
.info__time {
  font-size: 1.6rem;
  margin-right: 0;
  text-align: center;
}

}



